body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-habibi: Habibi;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-3xl: 22px;
  --font-size-mid: 17px;
  --font-size-2xs: 11px;

  /* Colors */
  --system-background-light-primary: #fff;
  --color-lightgray: #d1d3d9;
  --label-color-light-primary: #000;
  --color-darkgray: #abb0bc;
  --color-darkslategray-100: rgba(60, 60, 67, 0.6);
  --separator-color-light-with-transparency: rgba(60, 60, 67, 0.36);
  --color-whitesmoke: #e9e9eb;
  --color-royalblue: #007aff;

  /* Gaps */
  --gap-5xs: 8px;
  --gap-11xs: 2px;
  --gap-xs: 12px;
  --gap-8xs: 5px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-9xs: 4px;
  --padding-2xs: 11px;
  --padding-xs: 12px;
  --padding-7xs: 6px;
  --padding-4xs: 9px;
  --padding-base: 16px;

  /* Border radiuses */
  --br-81xl: 100px;
  --br-8xs-6: 4.6px;
  --br-45xl: 64px;
  --br-lg: 18px;
}
